@import "./utils";

.blogs {
	.hero {
		background-image: url(../images/blogs_hero.jpg);
		&_info {
			text-align: center;
		}
		&_tilte {
			text-align: center;
		}
	}

	.section.banner {
		height: 494px;
	}
	.banner_wrapper {
		background-color: rgba(73,17,251,0.05);
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: flex-start;
		background-image: url('../images/banner_img.svg');
		background-position: bottom right;
		background-repeat: no-repeat;
		background-size: auto;
		padding: 100px 70px;
	}

	.banner_title {
		font-family: helveticaBlack, sans-serif;
		font-size: 50px;
		line-height: 125%;
		margin-bottom: 25px;
		color: #4975FB;
	}

	.banner_text {
		font-size: 30px;
		line-height: 120%;
	}
}